.cart {
  min-height: calc(100vh - 100px - 96px);

  &__title {
    margin-bottom: 24px;
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    gap: 16px;

    @include on-desktop {
      flex-direction: row;
    }
  }

  &__total {
    width: 288px;
    height: 190px;
    border: 1px solid $gray-elements;
    padding: 24px;

    @include on-tablet {
      width: 592px;
      margin-bottom: 32px;
    }

    @include on-tablet-xl {
      width: 720px;
    }

    @include on-desktop {
      width: 368px;
      height: 206px;
      flex-direction: row;
    }

    &__prise {
      text-align: center;
      color: $gray-primary;
      font-size: 32px;
      font-weight: 700;
      letter-spacing: -0.32px;
    }

    &__text {
      position: relative;
      color: $gray-secondary;
      text-align: center;
      font-size: 14px;
      font-weight: 500;
      line-height: 21px;
      padding-bottom: 24px;
      margin-bottom: 24px;

      &::after {
        position: absolute;
        content: '';
        width: 249px;
        height: 1px;
        border-bottom: 1px solid $gray-elements;
        bottom: 0;
        left: 0;

        @include on-tablet {
          width: 540px;
        }

        @include on-tablet-xl {
          width: 670px;
        }

        @include on-desktop {
          width: 320px;
        }
      }
    }

    &__btn {
      width: 249px;
      height: 48px;
      background: $gray-primary;
      cursor: pointer;

      color: $gray-white;
      text-align: center;
      font-size: 14px;
      font-weight: 600;
      line-height: 21px;
      margin-bottom: 50px;

      @include on-tablet {
        width: 540px;
      }

      @include on-tablet-xl {
        width: 670px;
      }

      @include on-desktop {
        width: 320px;
      }
    }
  }

  &__massege {
    width: 320px;
    height: 48px;
    background-color: $gray-primary;
    color: $gray-white;
    text-align: center;
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
  }
}